	module.exports = {"data":{"markdownRemark":{"html":"<p>캘리포니아 대학 샌디에이고 대학의 글로벌 갈등 및 협력 연구소 (Institute for Global Conflict and Cooperation)는 팩스를 보내서 저를 홍콩으로 초청하여 학술 토론회에 참석했습니다. 회의의 주제는 매우 의미가 있습니다 : 중국계 : 중화 인민 공화국, 대만, 홍콩 및 마카오 간의 지역적 결과. 이것은 중국 경제 발전과 여러 지역에서 중국 공동체의 중요한 변화 이후에 논의 될 것입니다. 이 변화는이 지역의 정치 경제 및 문화 발전에 중요한 역할을하는 것으로 생각됩니다.</p>\n<p>컨퍼런스 토픽 주제는 다음과 같습니다.</p>\n<p>중국, 대만, 홍콩 간의 경제 상호 작용 모델 및 다른 아시아 태평양 경제 시스템과의 관계</p>\n<p>전자 또는 직물과 같은 다양한 산업 부문 간의 경제적 상호 작용;</p>\n<p>중국, 대만, 홍콩의 중국계 서클 간의 경제적 상호 작용의 경제적 및 정치적 중요성;</p>\n<p>동남아시아에서 중국인의 경제적 정치적 역할에 대한 중국 공동체의 경제적 기회의 역할, 중국 공동체에 대한 동남 아시아 인들의 정책 및 중국인에 대한 정책;</p>\n<p>아시아 중국 비즈니스 조직 및 활동 모델입니다.</p>\n<p>아시아 문화 단체에 대한 중국계의 영향;</p>\n<p>아시아 태평양 지역 경제 및 정치력 분배에서의 중국인의 의의</p>\n<p>이 주제들은 공부할 때 매우 필요합니다. 중국 경제 발전에 긍정적이지 않고 중국에 대한 두려움이있는 사람들이 있습니다.</p>\n<p>중국, 대만, 홍콩 정책에 대한 중국의 국내 경제 및 정치적 영향에 관한 주제를 다루겠다.</p>\n<p>불행히도 갈 시간이 없습니다.</p>","id":"/home/swain/workspace/political-life/src/pages/ko/0163.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ko/0163.md","frontmatter":{"title":"10월 20일 목요일"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ko/0163.md absPath of file >>> MarkdownRemark"}}

